import styled from 'styled-components';
import { keyframes } from 'styled-components';

const starAnim = keyframes`
  0% {
    transform: scale(0,0);
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    transform: scale(1,1);
    opacity: 1;
  }
`;

const fadeAnim = keyframes`
  0% {
    transform: scale(0,0);
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    transform: scale(1,1);
    opacity: 1;
  }
`

export const WhiteStars = styled.img`
  position: fixed;
  z-index: 7;
  overflow: hidden;
  /* max-width: 100%; */
  transform: translateY( ${({offsetY}) => -offsetY*0.1}px);
  transition: 4.5s;
  transition-timing-function: ease-in-out;
  animation: ${starAnim} 3s ease-in-out;
`;

