import { useEffect, useState } from 'react';
import * as S from './style';

export default function WindowIntroduction(props) {

  return (
    <S.Window>
      
      <S.Header>
        <S.Buttons>
          <S.Button red />
          <S.Button yellow />
          <S.Button green />
        </S.Buttons>
        <S.HeaderTitle>
          Introduction
        </S.HeaderTitle>
      </S.Header>

      <S.Code>

        <S.Paragraph>
          <S.Reserved>const</S.Reserved> 
          <S.Const> name</S.Const>
          <S.Reserved> =</S.Reserved>
          <S.String> '{props.data.name}'</S.String>
          <S.Span>;</S.Span>
        </S.Paragraph>

        <S.Paragraph>
          <S.Reserved>var</S.Reserved> 
          <S.Var> location</S.Var>
          <S.Reserved> =</S.Reserved>
          <S.String> '{props.data.location}'</S.String>
          <S.Span>;</S.Span>
        </S.Paragraph>

        <S.Paragraph>
          <S.Reserved>var</S.Reserved> 
          <S.Var> education</S.Var> 
          <S.Reserved> =</S.Reserved>
          <S.String> '{props.data.education}'</S.String>
          <S.Span>;</S.Span>
        </S.Paragraph>
  
        <S.Paragraph>
          <S.Reserved >var</S.Reserved> 
          <S.Var> occupation</S.Var> 
          <S.Reserved > =</S.Reserved>
          <S.String> '{props.data.occupation}'</S.String>
          <S.Span>;</S.Span>
        </S.Paragraph>

        <S.Paragraph>
          <S.Reserved >var</S.Reserved> 
          <S.Var> favoriteLanguages</S.Var> 
          <S.Reserved> =</S.Reserved>
          <S.Brackets> [</S.Brackets>
          {
            props.data.favoriteLanguages.map((item, index, array) => (
              <>
                {
                  index == 0 ? 
                    <S.String >'{item}'</S.String>
                    :
                    <>
                      {' '}
                      <S.String >'{item}'</S.String>
                    </>
                }
                {
                  index != (array.length-1) ? ',' : ''
                }
              </>
            ))
          }
          <S.Brackets >]</S.Brackets>
          <S.Span>;</S.Span>
        </S.Paragraph>
    
        <S.Paragraph>
          <S.Reserved >var</S.Reserved> 
          <S.Var> projects</S.Var>
          <S.Reserved> =</S.Reserved>
          <S.Brackets> [</S.Brackets>
          {
            props.data.projects.map((item, index, array) => (
              <>
                {
                  <S.Link 
                    smooth={true} 
                    duration={1000}
                    spy={true}
                    exact={true}
                    to={item.name}
                  >
                    {item.name}
                  </S.Link>
                }
                {
                  index != (array.length-1) ? ', ' : ''
                }
              </>
            ))
          }
          <S.Brackets>]</S.Brackets>
          <S.Span>;</S.Span>
        </S.Paragraph>
      
        <S.Paragraph>
          <S.Reserved>var</S.Reserved> 
          <S.Var> social</S.Var>
          <S.Reserved> =</S.Reserved>
          <S.Brackets> [</S.Brackets>

          {
            props.data.social.map((item, index, array) => (
              <>
                {   
                  <S.LinkOutside 
                    href={item.link}
                  >
                    {item.name}
                  </S.LinkOutside>  
                }
                {
                  index != (array.length-1) ? ', ' : ''
                }
              </>
            ))
          }
          <S.Brackets>]</S.Brackets>
          <S.Span>;</S.Span>
        </S.Paragraph>
        
      </S.Code>

    </S.Window>
  )
}